import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
export default {
	data() {
		return {
			name:'',
			global_page: 1, // 全球榜分页
			global_list: [], // 全球榜
			global_mylist: [],
			loading1:false,
			allow1:false,
			total:''
		}
	},
	onLoad(e) {
		this.name = e.name
		setTimeout(()=>{
			this.getinfo()
		}, 300);
	},
	// 下拉刷新
	onPullDownRefresh() {
		this.global_page = 1
		this.getinfo()
		setTimeout(function () {
			uni.stopPullDownRefresh();
			uni.showToast({
				title: '刷新成功',
				duration: 1000,
				icon:'none'
			});
		}, 2000);
	},
	// 触底加载
	onReachBottom(){
		// 第一页的上拉加载
		if(this.allow1){
			this.allow1 = false
			this.loading1 = true
			setTimeout(() => {
				this.global_page++
				this.getinfo()
				uni.showToast({
					title: '加载成功',
					duration: 1000,
					icon:'none'
				});
			  }, 1500)	
		}
	},
	methods: {
		// 发布跳转
		Issue(){
			if(uni.getStorageSync('setInfo') ===0){
				uni.showToast({
				    title: '请完善个人信息',
				    duration: 2000,
					icon:'none'
				});
				
				setTimeout(function() {
					uni.navigateTo({
						url:'../../pages/PersonalIdcard/PersonalIdcard'
					})
				}, 300);
				
			}else{
				if (uni.getStorageSync('isVip') === 0) {
					Dialog.confirm({
					  title: '提示',
					  message: '您还不是VIP用户,没有发布权限',
					  confirmButtonText:'去支付'
					}).then(() => {
						uni.navigateTo({
							url:'../../pagesMy/pay/pay'
						})
					}).catch(() => {
					  // on cancel
					});
				  } else if (uni.getStorageSync('isVip') === 1) {
					uni.navigateTo({
						url:'../../pagesTwo/TradingVolumeSend/TradingVolumeSend'
					})
				  } else {
					uni.clearStorageSync()
					uni.showToast({
					    title: '请登录',
					    duration: 2000,
						icon:'none'
					});
					
					uni.reLaunch({
						url: '../../pages/login/login'
					})
				  }
			}
			
		},
		goClub(id){
			if(id==0){
				uni.navigateTo({
					url:'../../pagesTwo/TradingClubFree/TradingClubFree?city='+this.name
				})
			}else{
				uni.navigateTo({
					url:'../../pagesTwo/TradingClub/TradingClub?id='+id
				})
			}
		},
		// 发布跳转
		Issue(){
			if(uni.getStorageSync('setInfo') ===0){
				uni.showToast({
				    title: '请完善个人信息',
				    duration: 2000,
					icon:'none'
				});
				
				setTimeout(function() {
					uni.navigateTo({
						url:'../../pages/PersonalIdcard/PersonalIdcard'
					})
				}, 300);
				
			}else{
				if (uni.getStorageSync('isVip') === 0) {
					Dialog.confirm({
					  title: '提示',
					  message: '您还不是VIP用户,没有发布权限',
					  confirmButtonText:'去支付'
					}).then(() => {
						uni.navigateTo({
							url:'../../pagesMy/pay/pay'
						})
					}).catch(() => {
					  // on cancel
					});
				  } else if (uni.getStorageSync('isVip') === 1) {
					uni.navigateTo({
						url:'../../pagesTwo/TradingVolumeSend/TradingVolumeSend'
					})
				  } else {
					uni.clearStorageSync()
					uni.showToast({
					    title: '请登录',
					    duration: 2000,
						icon:'none'
					});
					
					uni.reLaunch({
						url: '../../pages/login/login'
					})
				  }
			}
			
		},
		// 列表
		getinfo(){
			this.$api.city_rank_clublist({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				city:this.name,
				page: this.global_page,
				limit:30
		    }).then(res => {
				console.log(res)
				if(res.data.code === '200'){
					this.total = res.data.data.total
					if(this.global_page === 1){
						if(res.data.data.list.length>=30){
							this.global_list = res.data.data.list
							this.allow1 = true
							this.loading1 = false
						}else{
							this.global_list = res.data.data.list
							this.allow1 = false
							this.loading1 = false
						}														
					}else{
						if(res.data.data.list.length>=30){
							this.global_list = this.global_list.concat(res.data.data.list)
							this.allow1 = true
							this.loading1 = false
						}else{
							this.global_list = this.global_list.concat(res.data.data.list)
							this.allow1 = false
							this.loading1 = false
						}	
					}
				}
			})
		}
	}
}
